.card{
  display: grid;
  grid-template-columns: 1fr 5fr;
  border: 1px solid gray;
  border-radius: 1%;
  padding: 10px;
}

.card .description{
  font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.card img{
  height: 200px;
  width: 200px;
  object-fit: contain;
  margin: 20px;
}

.card:hover{
  border: 1px solid rgb(84, 84, 84);
}

.card_container{
  margin: 50px;
  display: grid;
  gap: 20px;
}

@media (max-width: 480px)  {
  .card{
    display: flex;
    flex-direction: column;
    border: 1px solid gray;
    border-radius: 1%;
    padding: 10px;
    align-items: center;
  }
}

.search-box{
  height: 30px;
  margin-left: 50px;
  margin-top: 50px;
  padding-left: 10px;
}

.search-box:focus{
  outline: none;
  border: 1px solid black;
  border-radius: 2px;
}